.scrollContainer{
    background-color: var(--bg2);
    color: white;
    /* padding: 2vmax 0; */
    padding-top: 5vmax;
}
.sec1Headingh2{
    color: var(--textC2) !important;
}
.productScroll{
    max-width: 80vw;
    min-width: 80%;
    height: 50vh;
    margin: 0 auto;
    padding: 3.5vmax 1vmax !important;
    display: flex;
    /* justify-content: center; */
    flex-direction: row;
    word-wrap: normal;
    gap: 1rem;
    /* border: 1px solid gray; */
    overflow-x: auto;
}
.productScroll::-webkit-scrollbar {
    /* width: 0px; Mostly for vertical scrollbars */
     height: 0px; /* Mostly for horizontal scrollbars */
    /* border-radius: 5px; */
  }
  .productScroll::-webkit-scrollbar-thumb { /* Foreground */
    background: #999;
  }
  .productScroll::-webkit-scrollbar-track { /* Background */
    background: #333;
  }
.SlideItem{
    max-width: 25vw;
    min-width: 20vw !important ;
    height: auto;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: center;
    padding: 1vmax;
    /* background: url(../../images/DeepBlue-5ml-Deepbluerub3.jpg);
    background-position-y: 15px;
    background-position-x: -2.5vmax;
    background-repeat: no-repeat;
    background-size: contain; */
    /* background-color: red; */
    gap: 1rem;
    /* border: 2px solid gray;   */
    transition: all 0.5s;
}
.nameBox{
    width: 100%;
    text-align: center;
    /* background-color: aqua; */
}
.nameBox > h3{
    font: 600 1.4vmax 'Roboto';
    color: var(--textC2);
    text-transform: uppercase
}
.ratingBox > p,
.nameBox > p{
    font: 300 0.9vmax 'Roboto';
    color: var(--textC2);
    
}
.ratingBox{
    width: 100%;
    text-align: center; 
    /* background-color: aqua; */

}
.react-stars{
    margin: 0 auto !important;
}
.SlideItem:hover{
    /* background-position-y: 0px; */
    margin-top: -10px;
    /* box-shadow: 1px 1px 10px gray; */
    /* border-bottom: 4px solid gray ; */

}
.SlideItem > h3{
    color: rgb(94, 94, 94);
    font-family: 'Poppins';

}
.Scrollborder{
    width: 5vw;
    height: 33%;
    /* background-color: black; */
    /* color: white; */
    display: flex;
    justify-content: center;
    align-items:end;
    position:absolute;
    left: 47% !important;
    z-index: 999;
    /* top: 0; */
    

}
.Scrollborder > div{
    width: 100%;
    height: 4px;
    /* border: 4px solid gray; */
    /* padding: 1vmax; */
    border-radius: 1rem;
    background-color: rgb(234, 234, 234);
    text-align: center;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
}



/* productbox2 end */

@media screen and (max-width:600px){


.productScroll{
    /* max-width: 65%; */
    margin: 3vmax 0;
    max-width: 100vw;
    max-height: 40vh !important;
    padding: 1vmax 1vmax;
    display: flex;
    /* flex-direction: column; */
    flex-direction: row;
    word-wrap: normal;
    gap: 1rem;
    /* border: 1px solid gray; */
    overflow-x: scroll;
    /* scrollbar-color: blue; */

}

.SlideItem{
    max-width: 100vw;
    min-width: 100% !important;
    height: 0 !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2vmax;
   
}
.nameBox > h3{
    font: 600 2.2vmax 'Roboto';
    text-transform: uppercase
}
.ratingBox > p,
.nameBox > p{
    font: 300 1.9vmax 'Roboto';
    
}
.Scrollborder{
    width: 12vw;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items:end;
    position:absolute;
    left: 45% !important;
    z-index: 999;

    

}
.Scrollborder > div{
    width: 100%;
    height: 4px;
    /* padding: 1vmax; */
    border-radius: 1rem;
    background-color: rgb(255, 255, 255);
    text-align: center;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
}

.SlideItem > h3{
    text-align: center;
    padding-right: 2.5vmax ;
}
.SlideItem:hover{
    /* background-position-y: 0px; */
    margin-top: 0px;
    /* box-shadow: 1px 1px 10px gray; */
    /* border-bottom: 4px solid gray ; */

}

}