.Sec2MainContainer{
    background-image: url(https://www.pepperscript.com/assets/images/background5.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.Section2Container{
    width: 100%;
    /* height: 90vh; */
    /* background-color: var(--bg2); */
    display: flex;
    padding: 5vmax 10vmax;
    gap: 1rem;
    /* justify-content: center; */
    /* align-items: center; */
}
.sec2Heading{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.sec2Heading >span{
    width: 15%;
    padding: 0.1vmax;
    background-color: var(--bg1);
}
.sec2Heading >h2{
    font: 600 1.7vmax 'Roboto';
    color: var(--textC1);
    text-align: center;
}
.Section2Container > .box1{
    width: 50%;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    /* padding: 2vmax 10vmax; */
    padding: 1.8vmax;
    padding-top: 3vmax;

}
.Section2Container > .box1 > img{
    /* width: 100%; */
    /* height: 80%; */
    box-shadow: 15px 15px 1px var(--bg2);
}
.Section2Container > .box2{
    width: 50%;
    display: flex;
    flex-direction: column;
    padding-right: 4vmax;
    /* box-shadow: 0 0 5px gray; */
    /* align-items: flex-start; */
    /* justify-content: center; */


}
.Section2Container > .box2 > h2{
    color: var(--textC3);
    font: 900 1.8vmax 'Poppins';
    font-family: 'Poppins';
    text-align: left;
    padding: 1vmax;
    background-color: var(--bg2);
    /* border-bottom: 2px solid var(--textC1); */
    border-left: 5px solid var(--bg1);
}
.Section2Container > .box2 > div{
    padding: 2vmax 0;
}
.Section2Container > .box2 > div >p{
    padding: 0.5vmax 0;
    font: 600 1.2vmax 'Roboto';
    color: var(--textC3);

}
.ptag{
    padding: 3vmax 0;
    line-height: 30px !important;
    font: 300 1.2vmax 'Roboto';
    color: var(--textC1);
    text-align: justify;
}
.join-Btn{
    padding: 0.8vmax 2vmax;
    font: 600 1.2vmax 'Roboto';
    border: none;
    background-color: var(--bg1);
    color: var(--textC3);
    transition: all 0.5s;
    cursor: pointer;
}
.join-Btn:hover{
    background-color: var(--bg1Hov);

}
.becomeKaksh{
    display: flex;
    flex-direction: column;
}
.becomeKaksh >h1{
    font: 900 4vmax 'Gotu';
}
/* form  */
.box2Form >  form{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.box2Form >  form >h2{
    width: 100%;
    padding: 1vmax;
    text-align: center;
    font: 600 2vmax 'Roboto';
    color: var(--textC1);
}
/* .FormSectionContainer > .box2 .inpStyle{ */

.box2Form >  form > input{ 
        width: 100%;
        height: 3vmax;
        padding: 0.8vmax;
        font: 600 1vmax 'poppins';
        color: var(--textC1);
        outline: none;
        border-radius: 0.2rem;
        border: none;
        background-color: white;
        border: 1px solid var(--textC2);
        margin: 0.5vmax 0;




}
.FormSectionBtn{
    background-color: var(--bg1) !important;
    color: white !important;

cursor: pointer;    transition: all 0.5s;
}
.FormSectionBtn:hover{
    background-color: var(--bg1Hov) !important;
}

@media screen and (max-width:650px){
    .Section2Container{
    width: 100%;
    /* height: 80vh; */
    /* background-color: ; */
    padding: 6vmax 1vmax;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    /* align-items: center; */
}
.Section2Container > .box1{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    /* padding-top: 14%; */
}

.Section2Container > .box1 > img{
    width: 100%;
}
.Section2Container > .box2{
    width: 100%;
    padding: 1vmax;
}
.Section2Container > .box2 > h2{
    /* color: rgb(46, 45, 45); */
    font: 900 2vmax 'Poppins';
}
.box2Form >  form > input{ 
    width: 100%;
    height: 6vmax;
    padding: 1.5vmax;
    font: 600 1.9vmax 'poppins';
    color: var(--textC1);
    outline: none;
    border-radius: 0.2rem;
    border: none;
    background-color: white;
    border: 1px solid var(--textC2);
    margin: 0.5vmax 0;




}
.becomeKaksh{
    display: flex;
    flex-direction: column;
}
.becomeKaksh >h1{
    font: 900 4.4vmax 'Gotu';
}

.sec2Heading >h2{
    font: 600 2.2vmax 'Roboto';
    color: var(--textC1);
    text-align: center;
}
.ptag{
    padding: 3vmax 0;
    line-height: 30px !important;
    font: 300 1.9vmax 'Roboto';
    color: var(--textC1);
    text-align: justify;
}
.join-Btn{
    padding: 0.8vmax 2vmax;
    font: 600 1.4vmax 'Roboto' !important;
    border: none;
    background-color: var(--bg1);
    color: var(--textC3);
    transition: all 0.5s;
    cursor: pointer;
    /* margin: 0 auto !important; */
}
}