.faydeContainer{
    width: 100%;
    padding: 4vmax 0vmax;
}

.faydeBox{
    width: 100%;
    padding: 3vmax 10vmax;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.faydeRow{
    display: flex;
    justify-content: space-between;
}

.faydeItem{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}
.faydeItem >div{
    width: 12vmax;
    height: 12vmax;
    border-radius: 50%;
    overflow: hidden;
    /* padding: ; */
    /* border: 4px solid var(--bg2); */
    background-color: var(--bg1);
    display: flex;
    justify-content: center;
    align-items: center;
}
.faydeItem > div > svg{
    font-size: 5vmax ;
    color: var(--bg2);
}
.faydeItem >p{
    font: 300 1.2vmax 'Popins';
    text-align: center;
    color: var(--textC1);
}

@media screen  and (max-width:600px){
    .faydeContainer{
        width: 100%;
        padding: 5vmax 0vmax;
    }
    
    .faydeBox{
        width: 100%;
        padding: 3vmax 1vmax;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        gap: 1rem;
    }
    .faydeRow{
        display: flex;
        /* flex-direction: column; */
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 1rem;
    }
    .faydeItem{
        width: 46%;
        /* height: 22vh; */
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 0.5rem;
        /* padding: 1vmax; */
    }
    .faydeItem >div{
        width: 15vmax;
        height: 15vmax;
        border-radius: 50%;
        overflow: hidden;
        /* padding: ; */
        border: 3.5px solid var(--bg1);
    }
    .faydeItem >p{
        font: 300 1.8vmax 'Roboto';
        color: var(--textC1);
    }
}