.Section1Container{
    padding: 8vmax 0;
}
.sec1Heading{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.sec1Heading >span{
    width: 25%;
    padding: 0.1vmax;
    background-color: var(--bg1);
}
.sec1Heading >h2{
    font: 600 1.7vmax 'Roboto';
    color: var(--textC1);
    text-align: center;
}
.sec1Box{
    padding: 0vmax 10vmax;
    padding-top: 4vmax;
    display: flex;
    gap: 4rem;
    justify-content: center;
    align-items: center;
}

.sec1Item{
    width: 30%;
    padding: 3.5vmax 3vmax;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    /* border: 1px solid rgb(231, 231, 231); */
    transition: all 0.8s;
    /* box-shadow: 10px 10px 20px gray; */
    box-shadow: 10px 10px 20px var(--bg1);


}
.sec1Item:hover{
    border: none;
    box-shadow: 10px 10px 20px var(--bg2);
}
.sec1Item > svg{
    font-size: 5vmax;
    color: var(--bg2);
}
.sec1Item > p{
    font: 600 1.7vmax 'Roboto';
    color: var(--bg2);   
}

@media screen and (max-width:600px){
    .sec1Heading{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.3rem;
    }
    .sec1Heading >span{
        width: 18%;
        padding: 0.1vmax;
        background-color: var(--bg1);
    }
    .sec1Heading >h2{
        font: 600 2.7vmax 'Roboto';
        color: var(--textC1);
        text-align: center;
    }
    .sec1Box{
        padding: 0vmax 2vmax;
        padding-top: 6vmax;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        justify-content: center;
        align-items: center;
    }

    .sec1Item{
        width: 100%;
        padding: 5vmax;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        justify-content: center;
        align-items: center;
        border: 1px solid rgb(231, 231, 231);
        transition: all 0.8s;
        /* box-shadow: 10px 10px 20px gray; */
    
    }   
    .sec1Item > svg{
        font-size: 6vmax;
        color: var(--bg1);
    }
    .sec1Item > p{
        font: 600 2.4vmax 'Roboto';
        color: var(--textC1);   
    }
}