@import url('https://fonts.googleapis.com/css2?family=Gotu&display=swap');
.FormSectionContainer{
    width: 100%;
    height: 90vh;
    background-color: var(--bg2);
    /* background-image: url(../../images/Hospitality.jpeg); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 4vmax;
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
}
.FormSectionContainer > .box1{
    width: 50%;
    display: flex;
    align-items: center;
    padding-top: 0vmax;
    padding-right: 10vmax;
    padding-left: 0vmax;
    /* justify-content: flex-start; */

}
.FormSectionContainer > .box1 > img{
    /* width: 80%; */
}
.FormSectionContainer > .box2{
    width: 50%;
    padding-top: 4vmax;
    padding-right: 1vmax;
    padding-left: 10vmax;
    /* background-color: ghostwhite; */
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    /* justify-content: center; */


}
.FormSectionContainer > .box2 > h2{
    color: var(--textC3);
    font: 900 3.9vmax 'Gotu';
}
.FormSectionContainer > .box2 > div{
    padding: 2vmax 0;
    display: flex;
    flex-direction: column;
}
.FormSectionContainer > .box2 > div >p{
    padding: 0.5vmax 0;
    font: 600 1.2vmax 'Roboto';
    color: var(--textC3);

}
.join-Btn{
    margin: 0.5rem 0;
    max-width: 30%;
    padding: 0.8vmax 2vmax;
    font: 600 1.2vmax 'Roboto';
    border: none;
    background-color: var(--bg1);
    color: var(--textC3);
    transition: all 0.5s;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
}
.join-Btn:hover{
    background-color: var(--bg1Hov);

}





   

@media screen and (max-width:650px){
    .FormSectionContainer{
    width: 100%;
    height: auto;
    /* background-color: ; */
    padding: 3vmax;
    padding-top: 9vmax;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    /* align-items: center; */
}
.FormSectionContainer > .box1{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    /* padding-top: 14%; */
}

.FormSectionContainer > .box1 > img{
    width: 80%;
}
.FormSectionContainer > .box2{
    width: 100%;
    padding: 1vmax;
}
.FormSectionContainer > .box2 > h2{
    color: var(--textC3);
    font: 900 4.8vmax 'Poppins' !important;
    font-family: 'Poppins';
}

.join-Btn{
    max-width: 40%;
    padding: 1.2vmax 4vmax !important;
    font: 600 1.8vmax 'Roboto' !important;
    border: none;
    background-color: var(--bg1);
    color: var(--textC3);
    transition: all 0.5s;
    cursor: pointer;
}
.FormSectionContainer > .box2 > div >p{
    padding: 0.5vmax 0;
    font: 600 1.8vmax 'Roboto';
    color: var(--textC3);
}
}