.contactSectionContainer{
    width: 100%;
    /* height: 90vh; */
    /* background-color: var(--bg2); */
    /* background-image: url(../../images/Hospitality.jpeg); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 4vmax;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
}
.contactSectionContainer > .box1{
    width: 50%;
    display: flex;
    /* align-items: center; */
    justify-content: flex-start;
    padding-left: 6vmax;

}
.contactSectionContainer > .box1 > img{
    width: 80%;
}
.contactSectionContainer > .box2{
    width: 50%;
    padding-right: 8vmax;
    padding-left: 0vmax;
    /* background-color: ghostwhite; */
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    /* justify-content: center; */


}
.contactSectionContainer > .box2 > h2{
    color: var(--textC3);
    font: 600 1.8vmax 'Poppins';
    font-family: 'Poppins';
}
.contactLinkBox{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.contactLinkBox > a{
    text-decoration: none;
    color: var(--textC3);
    font: 300 1.2vmax 'Roboto';
}
.contactSectionContainer > .box2 > div{
    padding: 1vmax 0;
}
.contactSectionContainer > .box2 > div >p{
    padding: 0.5vmax 0;
    font: 600 1.2vmax 'Roboto';
    color: var(--textC3);

}
.join-Btn{
    padding: 0.8vmax 2vmax;
    font: 600 1.2vmax 'Roboto';
    border: none;
    background-color: var(--bg1);
    color: var(--textC1);
    transition: all 0.5s;
    cursor: pointer;
}
.join-Btn:hover{
    background-color: var(--bg1Hov);

}

/* form  */
.contactSectionContainer > .box1 > form{
    width: 100%;
    height: 100%;
    /* background-color: aqua; */
    display: flex;
    flex-direction: column;
    /* justify-content: flex-start; */
    /* gap: 1rem; */
    /* margin: 1vmax 0; */
    /* padding-top: 1vmax; */
    padding-left: 1vmax;
    /* padding-right: 8vmax; */
}
.contactSectionContainer > .box1 > form >h2{
    width: 100%;
    padding: 1vmax;
    text-align: center;
    font: 600 2vmax 'Roboto';
    color: var(--textC1);
}
/* .FormSectionContainer > .box2 .inpStyle{ */
    .contactSectionContainer > .box1 > form > select,
.contactSectionContainer > .box1 > form > input{ 
        width: 100%;
        height: 3vmax;
        padding: 0.8vmax;
        font: 600 1vmax 'poppins';
        color: var(--textC1);
        outline: none;
        border-radius: 0.2rem;
        border: none;
        background-color: white;
        border: 1px solid var(--textC2);
        margin: 0.5vmax 0;




}
.FormSectionBtn{
    background-color: var(--bg1) !important;
    color: var(--textC1) !important;

cursor: pointer;    transition: all 0.5s;
}
.FormSectionBtn:hover{
    background-color: var(--bg1Hov) !important;
}



   

@media screen and (max-width:650px){
    .contactSectionContainer{
    width: 100%;
    height: auto;
    /* background-color: ; */
    padding: 2vmax;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    /* align-items: center; */
}
.contactSectionContainer{
    gap: 2rem;
}
.contactSectionContainer > .box1{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    /* padding-top: 14%; */
}

.contactSectionContainer > .box1 > img{
    width: 80%;
}
.contactSectionContainer > .box2{
    width: 100%;
    padding: 1vmax;
}
.contactLinkBox > a{
    text-decoration: none;
    font: 300 1.9vmax 'Roboto';
}
.contactSectionContainer > .box2 > h2{
    font: 900 4.8vmax 'Poppins' !important;
    font-family: 'Poppins';
}

.join-Btn{
    padding: 1.2vmax 4vmax !important;
    font: 600 1.8vmax 'Roboto' !important;
    border: none;
    background-color: var(--bg1);
    transition: all 0.5s;
    cursor: pointer;
}
.contactSectionContainer > .box2 > div >p{
    padding: 0.5vmax 0;
    font: 600 1.8vmax 'Roboto';
}

/* form */
.contactSectionContainer > .box1 > form >h2{
    width: 100%;
    padding: 1vmax;
    text-align: center;
    font: 600 2.5vmax 'Roboto';
}
.contactSectionContainer > .box1 > form > select,
.contactSectionContainer > .box1 > form > input{ 
        width: 100%;
        height: 6vmax;
        padding: 1.5vmax;
        font: 600 2vmax 'poppins';
        color: var(--textC1);
        outline: none;
        border-radius: 0.2rem;
        border: none;
        background-color: white;
        border: 1px solid var(--textC2);
        margin: 0.5vmax 0;

}
.contactSectionContainer > .box1 > form > select > option{
    width: 50% !important;
}
}