.voiceContainer {
  width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid #ccc;
    border-radius: 0.4rem;
    background-color: white;
    padding: 1vmax;
    gap: 1rem;

  }
.voiceContainer >div{
  width: 100%;
  display: flex;
  gap: 1rem;
  /* justify-content: space-between; */
}
  .voiceContainer > div > button {
    padding: 0.5vmax 1vmax;
    /* margin: 5px; */
    border-radius: 5px;
    font: 500 1vmax 'Roboto';
    cursor: pointer;
    outline: none;
    border: none;
    background: var(--bg1);
    color: #fff;
  }
  .voiceContainer > div > span{
    /* background-color: blue; */
    padding: 0.5vmax 1.5vmax;
    width: 40%;
    color: var(--bg2);
    font-weight: 900;
    display: flex;
    align-items: center;
    gap:  1rem;

  }
  
  .timer {
    font-weight: bold;
  }
  
  .audio {
    margin: 10px 0;
  }
  